@font-face {
  font-family: 'Open Sans';
  src: url("../fonts/opensans/opensanslight.woff2") format("woff2"), url("../fonts/opensans/opensanslight.woff") format("woff"), url("../fonts/opensans/opensanslight.ttf") format("truetype");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'PT Sans';
  src: url("../fonts/ptsans/ptsans.woff2") format("woff2"), url("../fonts/ptsans/ptsans.woff") format("woff"), url("../fonts/ptsans/ptsans.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'PT Sans';
  src: url("../fonts/ptsans/ptsansbold.woff2") format("woff2"), url("../fonts/ptsans/ptsansbold.woff") format("woff"), url("../fonts/ptsans/ptsansbold.ttf") format("truetype");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

html, body {
  height: 100%;
}

body {
  height: 100%;
  font-size: 14px;
  font-family: "PT Sans", sans-serif;
  font-weight: 400;
  color: #000000;
  overflow-x: hidden;
}

ul, ol {
  list-style-type: none;
  padding: 0;
}

a {
  display: block;
  font-family: inherit;
  text-decoration: none;
  color: inherit;
  transition: all .3s ease-in-out;
  cursor: pointer;
}

a:hover {
  text-decoration: none;
}

a:active {
  outline: none;
}

a:focus {
  outline: none;
}

input {
  background: transparent;
  border: none;
  outline: none;
  transition: all .3s ease-in-out;
}

input:hover, input:active, input:focus {
  border: none;
  outline: none;
}

button {
  cursor: pointer;
  outline: none;
}

button:active {
  outline: none;
}

button:focus {
  outline: none;
}

img {
  max-width: 100%;
  height: auto;
}

select {
  outline: none;
}

select:focus {
  outline: none;
}

button,
input,
select,
textarea {
  font-family: inherit;
  border: none;
  cursor: pointer;
}

blockquote,
body,
dd,
dl,
figcaption,
figure,
h1,
h2,
h3,
h4,
li,
ol,
p,
ul {
  margin: 0;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus
input:-webkit-autofill,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover
textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
  border: 0;
  -webkit-text-fill-color: #сссccc;
  -webkit-box-shadow: 0 0 0px 1000px transparent inset;
  transition: background-color 5000s ease-in-out 0s;
  background: -webkit-linear-gradient(top, rgba(255, 255, 255, 0) 0%, rgba(0, 174, 255, 0.04) 50%, rgba(255, 255, 255, 0) 51%, rgba(0, 174, 255, 0.03) 100%);
}

.g-container {
  width: 100%;
  padding: 0 15px;
  margin: auto;
  max-width: 1060px;
}

@media (max-width: 1060px) {
  .g-container {
    max-width: 100%;
  }
}
